// Font Sizes

.fs12 {
  font-size: rem(12px);
}
.fs14 {
  font-size: rem(14px);
}
.fs18 {
  font-size: rem(18px);
}
.fs20 {
  font-size: rem(20px);
}
.fs24 {
  font-size: rem(24px);
}
.fs28 {
  font-size: rem(28px);
}
.fs30 {
  font-size: rem(30px);
}

// flex helpers

.flex {
  display: flex;

  > *.full {
    flex: 1;
  }
  &.col {
    flex-direction: column;
  }
  &.row {
    flex-direction: row;
  }
  &.justify-center {
    justify-content: center;
  }
  &.align-center {
    align-items: center;
  }
  &.space-between {
    justify-content: space-between;
  }
  &.center {
    align-items: center;
    justify-content: center;
  }
}

.grid-center {
  display: grid;
  place-items: center;
}
// Media helpers

.square-ratio {
  @include aspect-ratio(1, 1);
}
.video-ratio {
  @include aspect-ratio(16, 9);
}
.portrait-ratio {
  @include aspect-ratio(9, 16);
}

// li

.clear-list {
  list-style-type: none;
  padding-left: 0;
}

.hidden {
  visibility: hidden;
  opacity: 0;
  max-height: 0;
}
.display-none {
  display: none;
}
